import Vue from 'vue'
import Router from 'vue-router'
import no1 from './routes/no1.vue'
import no2 from './routes/no2.vue'
import no3 from './routes/no3.vue'
import no1_1 from './routes/no1_1.vue'
import no1_2 from './routes/no1_2.vue'
import no1_3 from './routes/no1_3.vue'
import no1_4 from './routes/no1_4.vue'
import no1_5 from './routes/no1_5.vue'
import no1_6 from './routes/no1_6.vue'
import no1_7 from './routes/no1_7.vue'
import no1_8 from './routes/no1_8.vue'
import no1_9 from './routes/no1_9.vue'
import no1_10 from './routes/no1_10.vue'
import no1_11 from './routes/no1_11.vue'
//1.  vue  vue-Router



//2. 

// import no2 from './routes/no2.vue'
// import no3 from './routes/no3.vue'

//3.
Vue.use(Router)

//4.

var routes = [
	{
		path:'/',
		name:'no1',
		component:no1,
	},
	{
		path:'/no2',
		name:'no2',
		component:no2,
	},
	{
		path:'/no3',
		name:'no3',
		component:no3,
	},
	{
		path:'/no1_1',
		name:'no1_1',
		component:no1_1,
	},
	{
		path:'/no1_2',
		name:'no1_2',
		component:no1_2,
	},
	{
		path:'/no1_3',
		name:'no1_3',
		component:no1_3,
	},
	{
		path:'/no1_4',
		name:'no1_4',
		component:no1_4,
	},
	{
		path:'/no1_5',
		name:'no1_5',
		component:no1_5,
	},
	{
		path:'/no1_6',
		name:'no1_6',
		component:no1_6,
	},
	{
		path:'/no1_7',
		name:'no1_7',
		component:no1_7,
	},
	{
		path:'/no1_8',
		name:'no1_8',
		component:no1_8,
	},
	{
		path:'/no1_9',
		name:'no1_9',
		component:no1_9,
	},
		{
		path:'/no1_10/:uid',
		name:'no1_10',
		component:no1_10,
	},
	{
		path:'/no1_11/:uid',
		name:'no1_11',
		component:no1_11,
	},
]


//5.
export default new Router({
	routes
})


